.angle-base {
    width: 100px;
    height: 50px;
    background-color: steelblue;
    margin-top: 30px;
}

.angle-deg {
    transform: rotate(45deg);
}

.angle-grad {
    transform: rotate(50grad);
}

.angle-rad {
    transform: rotate(0.7854rad);
}

.angle-turn {
    transform: rotate(0.125turn);
}

video::backdrop {
    background-color: lightgreen;
}

.backdrop-filter-base {
    display: flex;
    align-items: center;
    justify-content: center;
}

.backrdop-filter-image {
    width: 300px;
    height: 168.8px;
}

.backdrop-filter-blur {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
}

.backdrop-filter-brightness {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: brightness(0.1);
}

.backdrop-filter-contrast {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: contrast(0.5);
}

.backdrop-filter-drop-shadow {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    /* 上手く動いてない気がする,要素としては存在してる */
    backdrop-filter: drop-shadow(30px 10px 10px blue);
}

.backdrop-filter-grayscale {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: grayscale(100%);
}

.backdrop-filter-hue-rotate {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: hue-rotate(90deg);
}

.backdrop-filter-invert {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: invert(1);
}

.backdrop-filter-opacity {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: opacity(0.1);
}

.backdrop-filter-saturate {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: saturate(0);
}

.backdrop-filter-sepia {
    position: absolute;
    width: 150px;
    height: 84.4px;
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: sepia(1);
}

.backface-visibility-image {
    width: 300px;
    height: 168.8px;
}

.backface-visibility-visiable {
    backface-visibility: visible;
    animation: backface-visibility-rotater 10s infinite ease 1s;
}

.backface-visibility-hidden {
    backface-visibility: hidden;
    animation: backface-visibility-rotater 10s infinite ease 1s;
}

@keyframes backface-visibility-rotater {
    0% { transform: rotateY(0deg);}
    100% { transform: rotateY(360deg);}
}

.background-attachment-viewport {
    width: 250px;
    height: 250px;
    background-image: url('../assets/akabeko.jpg');
    background-size: contain;
    overflow: scroll;
}

.background-attachment-scroll {
    background-attachment: scroll;
}

.background-attachment-fixed {
    background-attachment: fixed;
}

.background-attachment-local {
    background-attachment: local;
